*, *::after, *::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

h1 {
  margin-bottom: 0; }

ul {
  list-style: none;
  margin-bottom: 0; }

.navbar-light .navbar-toggler {
  border: none; }

.card-header {
  display: flex;
  flex-direction: column;
  justify-content: center; }

.btn-primary {
  background-color: #0056B3; }

h5.card-title {
  white-space: pre-wrap; }

div.dropdown-menu.show {
  left: -10vw; }

#moreInfoButton {
  font-size: 1rem;
  min-width: -webkit-min-content;
  min-width: -moz-min-content;
  min-width: min-content;
  margin: 0;
  padding: 0.25rem 0.75rem;
  width: auto; }

.moreInfo h5 {
  margin-top: 0.5rem; }

.moreInfo p {
  padding: 0.5rem 0; }

section[id*="MoreInfo"] section h5, section[id*="MoreInfo"] section p {
  margin: 0.25rem 0; }

section[id*="MoreInfo"] section h5:last-of-type {
  margin-top: 0.5rem; }

.noPhotoAvailable {
  height: 200px;
  max-height: -webkit-min-content;
  max-height: -moz-min-content;
  max-height: min-content; }

.app {
  display: grid;
  grid-template-columns: 2fr 10fr;
  grid-template-rows: minmax(2rem, 5rem) 10fr 1fr;
  min-width: 100vw;
  min-height: 100vh;
  overflow-x: hidden; }

header {
  background: #eee;
  display: flex;
  align-items: center;
  grid-area: 1 / 1 / 2 / 13; }
  header .navbar {
    display: grid;
    grid-template-columns: 1fr 5fr;
    grid-gap: 0.5rem;
    gap: 0.5rem;
    flex: 1 1 100%;
    padding: 0; }
    header .navbar #brand {
      background: #eee;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 0; }
      header .navbar #brand h1 {
        color: #007bff; }
    header .navbar #mainNav {
      background: #eee;
      margin-left: auto;
      margin-right: 3rem; }
      header .navbar #mainNav ul {
        display: flex;
        align-items: center; }

main {
  display: grid;
  grid-area: 2 / 1 / 12 / 13;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: auto;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  text-align: center; }
  main .addressForm {
    grid-area: 1 / 1 / 3 / 2; }
  main .addressForm {
    padding: 0 2rem; }
    main .addressForm h2 {
      font-size: 1.5rem;
      margin: 0.75rem auto 0; }
    main .addressForm form {
      margin: 0 auto 2rem; }
      main .addressForm form .form-group {
        margin: 0; }
      main .addressForm form button {
        margin: 2rem 0;
        width: 100%; }
  main .registrationAddress {
    display: flex;
    flex-direction: column;
    grid-area: 1 / 1 / 3 / 2; }
    main .registrationAddress .card-body {
      flex: 0 1 auto;
      padding: 0; }
      main .registrationAddress .card-body .userAddress {
        padding: 2rem 1rem; }
        main .registrationAddress .card-body .userAddress ul li {
          font-size: 1.33rem;
          font-weight: 300;
          line-height: 1.5; }
      main .registrationAddress .card-body .changeAddress {
        border-bottom: 1px solid rgba(0, 0, 0, 0.125);
        border-top: 1px solid rgba(0, 0, 0, 0.125);
        border-radius: 0.25rem; }
        main .registrationAddress .card-body .changeAddress button {
          margin: 1rem 0;
          padding: 1rem; }
      main .registrationAddress .card-body .electionAuthorityAddress {
        background: #eee;
        padding: 3rem 1rem 2rem; }
  main .votingInformationPlaceholder {
    color: #6c757d;
    display: flex;
    flex-direction: column;
    grid-area: 1 / 2 / 3 / 7;
    padding: 8rem 2rem; }
  main .dataOutput {
    display: flex;
    flex-direction: column;
    grid-area: 1 / 2 / 3 / 7; }
  main .votingInformation {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto;
    grid-area: 1 / 1 / 2 / 7;
    grid-gap: 0.5rem;
    gap: 0.5rem;
    margin-bottom: 0.5rem; }
    main .votingInformation .voterInformation {
      grid-area: 1 / 1 / 2 / 2;
      height: -webkit-max-content;
      height: -moz-max-content;
      height: max-content; }
      main .votingInformation .voterInformation > .card-body {
        display: flex;
        flex-direction: row;
        justify-content: space-between; }
    main .votingInformation .voteInfoLinks {
      display: flex;
      flex-direction: column; }
      main .votingInformation .voteInfoLinks h4 {
        margin-bottom: 2rem; }
      main .votingInformation .voteInfoLinks ul {
        display: flex;
        justify-content: space-around; }
        main .votingInformation .voteInfoLinks ul li {
          margin: 0 1rem; }
          main .votingInformation .voteInfoLinks ul li a {
            font-size: 1.33rem; }
    main .votingInformation .electedRepresentatives {
      grid-area: 2 / 1 / 3 / 2; }
  main .electionsInformation {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 0.5rem;
    gap: 0.5rem;
    grid-area: 2 / 1 / 3 / 7;
    margin-bottom: 1rem; }

.representativesCard button, .officeRacesCard button, .upcomingRacesCard button, .upcomingReferendumCard button {
  font-size: 1.25rem;
  margin: 1rem;
  min-width: 250px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content; }

.referendum button {
  max-width: 50%; }

.federalRepresentatives, .stateRepresentatives, .localRepresentatives {
  display: grid; }
  .federalRepresentatives > .card, .stateRepresentatives > .card, .localRepresentatives > .card {
    display: flex;
    flex-direction: row; }
    .federalRepresentatives > .card > .card, .stateRepresentatives > .card > .card, .localRepresentatives > .card > .card {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 100%; }
      .federalRepresentatives > .card > .card > .card-header, .federalRepresentatives > .card > .card > .card-body, .stateRepresentatives > .card > .card > .card-header, .stateRepresentatives > .card > .card > .card-body, .localRepresentatives > .card > .card > .card-header, .localRepresentatives > .card > .card > .card-body {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%; }
      .federalRepresentatives > .card > .card > .card-body figure img, .stateRepresentatives > .card > .card > .card-body figure img, .localRepresentatives > .card > .card > .card-body figure img {
        height: 200px;
        width: 175px;
        object-fit: cover; }
      .federalRepresentatives > .card > .card > .card-header, .stateRepresentatives > .card > .card > .card-header, .localRepresentatives > .card > .card > .card-header {
        min-height: 6rem;
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content; }

.stateRepresentatives > .card > .card > .card-header {
  min-height: 7rem; }

.localRepresentatives > .card > .card > .card-header {
  min-height: 9rem; }

.federalRepresentatives {
  grid-template-columns: repeat(2, 1fr) 2fr 1fr; }
  .federalRepresentatives .card-header {
    min-height: -webkit-fit-content;
    min-height: -moz-fit-content;
    min-height: fit-content; }

.stateRepresentatives {
  grid-template-columns: repeat(5, 1fr); }

.localRepresentatives {
  grid-template-columns: repeat(5, 1fr); }
  .localRepresentatives > .card {
    display: contents; }

.raceCandidates > section > .card-body {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr)); }
  .raceCandidates > section > .card-body > .card > .card-body {
    display: flex;
    flex-direction: column;
    align-items: center; }

footer {
  background: #cdcdcd;
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-area: 12 / 1 / 13 / 13; }
  footer h3 {
    color: #333;
    padding: 1rem 0; }
  footer .subfooter {
    background: #dedede;
    justify-content: space-around;
    padding: 1rem 0;
    width: 100%; }

.socialLinks {
  display: flex; }
  .socialLinks li {
    margin: 0.25rem; }
    .socialLinks li a i {
      font-size: 1.66rem; }

@media only screen and (max-width: 1000px) {
  header .navbar {
    grid-template-columns: 1fr 1fr; }
    header .navbar #brand {
      margin-right: 0;
      margin-left: 1rem;
      justify-self: flex-start; }
    header .navbar button.navbar-toggler {
      justify-self: flex-end; }
    header .navbar #mainNav {
      grid-area: 2 / 2 / 3 / 3;
      margin-right: 0;
      padding: 0.25rem 0.75rem; }
      header .navbar #mainNav ul {
        align-items: flex-end; }
    header .navbar #navbarDropdown {
      align-self: flex-end; }
    header .navbar .nav-item {
      width: inherit; }
    header .navbar .nav-item.dropdown {
      display: flex;
      flex-direction: column;
      align-items: center; }
  main {
    grid-template-columns: repeat(12, 1fr); }
    main .addressForm {
      grid-area: 1 / 1 / 3 / 4; }
    main .registrationAddress {
      grid-area: 1 / 1 / 3 / 4; }
    main .votingInformationPlaceholder, main .dataOutput {
      grid-area: 1 / 4 / 3 / 13; } }

@media only screen and (max-width: 600px) {
  .app {
    display: flex;
    flex-direction: column; }
  main {
    display: flex;
    flex-direction: column; }
    main .addressForm, main .registrationAddress, main .dataOutput, main .votingInformation, main .electionsInformation {
      display: flex;
      flex-direction: column;
      grid-area: none; }
    main .informationPlaceholder {
      padding: 4rem 2rem; }
    main .votingInformation .voteInfoButtons {
      flex-direction: column; }
      main .votingInformation .voteInfoButtons ul {
        flex-direction: column;
        margin: 0; }
        main .votingInformation .voteInfoButtons ul li {
          margin: 0.5rem 0; }
    main .federalRepresentatives, main .stateRepresentatives {
      grid-template-columns: repeat(2, 1fr); } }

